<template>
  <div class="app loginBg flex-row align-items-center">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="card-group mb-0">
            <div class="card p-2">
              <div class="card-block">
                <h1>Login</h1>
                <p class="text-muted">Sign In to your account</p>
                <div class="input-group mb-1">
                  <span class="input-group-addon">
                    <i class="icon-user"></i>
                  </span>
                  <input type="text" class="form-control" v-model="username" placeholder="Username">
                </div>
                <div class="input-group mb-2">
                  <span class="input-group-addon">
                    <i class="icon-lock"></i>
                  </span>
                  <input type="password" class="form-control"  v-model="password" placeholder="Password">
                </div>
                <div class="row">
                  <div class="col-12">

                    <!-- <button v-if="IsRemember"  type="button" v-on:click="rememberMe" class="btn btn-secondary btn-info px-2" style="margin-right:5px;">
                      <i class="fa fa-check"></i>
                      Remember Me
                    </button>
                      <button v-else type="button" v-on:click="rememberMe" class="btn btn-secondary btn-default px-2" style="margin-right:5px;">
                      <i class="fa fa-circle-o"></i>
                      Remember Me
                    </button> -->

                    <button type="button" v-on:click="() => {login()}" class="btn btn-primary px-2">Login</button>
                  </div>
                </div>
              </div>
            </div>
            <div class="card card-inverse card-primary py-3 hidden-md-down" style="width:44%">
              <div class="card-block text-center ">
                <!-- <div>
                  <img src="../../static/img/logo.png">
                </div> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//  import router from '../router'
import ConfigHelper from "../helpers/config.helper";
import UserService from "../service/user.service";

export default {
  name: "Login",
  props: {
    IsRemember: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data(){
    return{
       username: '',
        password: ''
    }
  },
  mounted() {
    this.initWebsite();
  },
  methods: {
    initWebsite() {},
    loginSkip: function loginSkip(){
        this.$router.push("settings/config");
    },
    login: function login() {
      UserService.login({
        username: this.username,
        password: this.password
        // username: 'Admin',
        // password: '123456'
      }).do(res => {
        if (res) {
          this.$router.push("settings/config");
          return;
        }
        alert('Login Error')
      })
      .toPromise();
    },
    rememberMe: function remember() {
      this.IsRemember = !this.IsRemember;
    }
  }
};
</script>



