<template>
  <div class="row">
    <div class="col-md-12">
      <div class="form-group">
        <input type="button" class="btn btn-primary btn-lg btn-block" v-on:click="() => {showCreateFloor(true)}" value="Click to Create New Floor" />
      </div>
    </div>
    <div class="col-md-2">
      <div class="card">
        <div class="card-header">
          Floor List
        </div>
        <div class="card-block">
          <div class="floor-box" v-bind:class="{active:isSelectedFloor(floor)}" v-for="floor in $data.floorList" v-on:click="() => {selectFloor(floor)}" :key="floor.objectId">
            <h4>Floor {{floor.floor}}</h4>
          </div>
        </div>
      </div>
    </div>
    <div class="col-md-10">
      <div class="card">
        <div class="card-header">
          Floor Details
        </div>
        <div class="row">
          <div class="col-md-8">
            <div v-if="alreadySelected" class="card-block">
              <h4>Floor number
                <span class="text-primary"> {{this.currentFloor.floor}}</span>
              </h4>
              <hr/>
              <div class="form-group">
                <label>Floor Name</label>
                <input type="text" class="form-control" v-model="$data.currentFloor.name" />
              </div>
              <div class="floor-panel">
                <img v-if="($data.currentFloor.image)" :src="getCurrentImage" />
                <div v-else>
                  <h1>No Image Display</h1>
                </div>
              </div>
              <div class="upload-panel">
                <input type="file" accept=".png, .jpg, .jpeg" v-on:change="(e) => {setUpdateImage(e)}" />
              </div>
              <hr/>
              <div>
                <input type="button" class="btn btn-primary" value="SAVE" v-on:click="() => {saveFloor()}">
                <input type="button" class="btn btn-danger pull-right" value="DELETE" v-on:click="() => {deleteFloor()}">
                <!-- <input type="button" class="btn btn-warning pull-right" value="CANCEL" v-on:click="() => {cancelFloorSetting()}"> -->
              </div>
            </div>
            <div v-else class="card-block">
              <h4>Please Select Floor</h4>

            </div>
          </div>
          <div class="col-md-4">
            <div v-if="alreadySelected" class="card-block">
              <h4>Camera List</h4>
              <hr/>
              <div class="camera-list">
                <div class="camera-item" v-for="camera in this.currentFloor.cameras" :key="camera.sourceid" v-on:click="() => {selectCamera(camera)}">
                  <h5>{{camera.name}}</h5>
                  <hr/>
                  <div class="camera-info">
                    <label> X-Axis
                      <span>{{camera.x}}</span>
                    </label>
                    <label> Y-Axis
                      <span>{{camera.y}}</span>
                    </label>
                    <label> Angle
                      <span>{{camera.angle}}</span>
                    </label>
                  </diV>
                </div>
                <div class="form-group">
                  <input type="button" class="btn btn-primary btn-lg btn-block" v-on:click="() => {showCameraDetail(true)}" value="Add Camera" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-bind:class="{active:showMask}" class="wb-mask">
      <div class="card wb-mask-item">
        <div class='card-header'>
          <h4> Add Floor</h4>
          <div class="card-actions">
            <button class="btn-close" v-on:click="() => {showCreateFloor(false)}">
              close
            </button>
          </div>
        </div>
        <div class='card-block  map-info-block'>
          <div class="form-group">
            <label>Floor</label>
            <input type="number" class="form-control" v-model="$data.newFloor.floor" />
          </div>
          <div class="form-group">
            <label>name</label>
            <input type="text" class="form-control" v-model="$data.newFloor.name" />
          </div>
          <div class="form-group">
            <label>Map Upload</label>
            <input type="file" class="form-control" accept=".png, .jpg, .jpeg" v-on:change="(e) => {setImage(e)}" />
          </div>
          <div class="floor-panel">
            <img v-if="(this.uploadImg)" :src="this.uploadImg" />
          </div>
        </div>
        <div class="card-footer">
          <input type="button" class="btn btn-primary pull-right" value="SAVE" v-on:click="() => {createFloor()}" />
        </div>
      </div>
    </div>
    <div v-bind:class="{active:showCameraMask}" class="wb-mask">
      <div class="card wb-mask-item camera-mask-item">
        <div class='card-header'>
          <h4 v-if="this.editMode == 'ADD'"> Add Camera</h4>
          <h4 v-if="this.editMode == 'EDIT'"> Camera Detail</h4>
          <div class="card-actions">
            <button class="btn-close" v-on:click="() => {showCameraDetail(false)}">
              close
            </button>
          </div>
        </div>
        <div class='card-block cemera-info-block'>
          <div class="floor-panel">
            <div class="camera-position" :style="{left:this.MapInfomation.MapPostionX+'px',top: this.MapInfomation.MapPostionY + 'px',transform:'rotate(' + this.MapInfomation.CameraAngle + 'deg)'}">

            </div>
            <img v-if="(this.currentFloor.image)" ref="CameraFloorMap" :src="getCurrentImage" />
            <div v-else>
              <h1>No Image Display</h1>
            </div>
          </div>
          <div class="camera-info-box row">
            <div class="col-md-6">
              <div>
                <h4>Camra Information</h4>
                <hr/>
                <div class="form-group">
                  <label>Name</label>
                  <input type="text" class="form-control" v-model="$data.currentCamera.name" />
                </div>
                <div class="form-group">
                  <label>Source ID</label>
                  <input type="text" class="form-control" v-model="$data.currentCamera.sourceid" />
                </div>
              </div>
            </div>
            <div class="col-md-6">
              <div>
                <h4>Camra Position</h4>
                <hr/>
                <div class="form-group">
                  <label>X-Axis</label>
                  <!-- <input type="number" class="form-control" v-model="$data.currentCamera.x" /> -->
                  <input type="number" class="form-control" v-model="currentCameraX" />
                </div>
                <div class="form-group">
                  <label>Y-Axis</label>
                  <input type="number" class="form-control" v-model="currentCameraY" />
                </div>
                <div class="form-group">
                  <label>Angle</label>
                  <input type="number" class="form-control" v-model="currentCameraAngle" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card-footer">
          <input type="button" class="btn btn-primary" value="SAVE" v-on:click="() => {saveCamera()}" />
          <input type="button" class="btn btn-danger pull-right" value="DELETE" v-on:click="() => {deleteCamera()}" />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SettingsService from "../../service/settings.service";
import UserService from "../../service/user.service";
import ApiService from "../../service/api.service";

export default {
  name: "floor-settings",
  data() {
    return {
      showMask: false,
      showCameraMask: false,
      floorList: {},
      currentFloor: {},
      currentCamera: {},
      newFloor: {},
      selectedFloor: "",
      alreadySelected: false,
      editMode: "",
      newFloor: {
        floor: 1,
        name: "Floor"
      },
      uploadImg: "",
      uploadEditImg: "",
      cameraPosition: {
        x: 0,
        y: 0
      },
      currentCameraX: 0,
      currentCameraY: 0,
      currentCameraAngle: 0,
      MapInfomation: {
        MapPostionX: 0,
        MapPostionY: 0,
        MapWidth: 0,
        MapHeight: 0,
        MapClientWidth: 0,
        MapClientHeight: 0,
        CameraAngle: 0
      }
    };
  },
  mounted() {
    this.getFloorList();
  },
  computed: {
    getCurrentImage() {
      return this.currentFloor.image.replace("localhost", ApiService.host);
    },
    getCameraX() {
      console.log(this.currentCamera.x);
    },
    getCameraY() {
      console.log(this.currentCamera.y);
    }
  },
  watch: {
    currentCameraX: {
      immediate: false,
      handler(newValue, oldValue) {
        console.log(this.currentCamera.x);
        console.log("newValue" + newValue);
        console.log("oldValue" + oldValue);

        this.currentCamera.x = newValue;

        const radio =
          this.$refs.CameraFloorMap.naturalWidth /
          this.$refs.CameraFloorMap.clientWidth;
        this.MapInfomation.MapPostionX = newValue / radio - 25;
        console.log("naturalHeight" + this.$refs.CameraFloorMap.naturalHeight);
        // this.MapInfomation.MapPostionX = ()
      }
    },
    currentCameraY: function(newValue, oldValue) {
      console.log(this.currentCamera.y);
      console.log(newValue);
      console.log(oldValue);

      this.currentCamera.y = newValue;

      const radio =
        this.$refs.CameraFloorMap.naturalHeight /
        this.$refs.CameraFloorMap.clientHeight;
      this.MapInfomation.MapPostionY = newValue / radio - 25;
    },
    currentCameraAngle: function(newValue, oldValue) {
      this.currentCamera.angle = newValue;
      this.MapInfomation.CameraAngle = newValue;
    }
  },
  methods: {
    setCameraPosition: function SetCameraPosition(X, Y, Angle) {
      console.log("Hi Jack");

      const radioX =
        this.$refs.CameraFloorMap.naturalWidth /
        this.$refs.CameraFloorMap.clientWidth;
      this.MapInfomation.MapPostionX = X / radioX - 25;

      const radioY =
        this.$refs.CameraFloorMap.naturalHeight /
        this.$refs.CameraFloorMap.clientHeight;
      this.MapInfomation.MapPostionY = Y / radioY - 25;

      this.currentCamera.angle = Angle;
      this.MapInfomation.CameraAngle = Angle;
      console.log(this.MapInfomation);
    },
    cancelFloorSetting: function CancelFloorSetting() {
      // console.log('123');
      // this.currentFloor = this.currentFloorBak;
      // console.log(this.currentFloorBak);
      this.getFloorList();
    },
    getFloorList: function GetFloorList() {
      SettingsService.floor_get_all_floors({ sessionId: UserService.sessionId })
        .switchMap(res => res.json())
        .map(floorData => {
          const data = JSON.stringify(floorData);
          this.floorList = floorData.results.sort((a, b) => {
            return a.floor - b.floor;
          });
          console.log("floorData :", this.floorList);
        })
        .toPromise();
    },

    showCreateFloor: function ShowCreateFloor(flag) {
      // console.log('Hi');
      this.showMask = flag;
    },
    showCameraDetail: function ShowCameraDetail(flag) {
      if (!flag) {
        this.currentCamera = {};
      
      }
      this.editMode = "ADD";
        this.currentCameraX = 0;
        this.currentCameraY = 0;
        this.currentCameraAngle = 45;
      this.showCameraMask = flag;
    },
    selectFloor: function SelectFloor(floor) {
      // console.log('Hi');
      this.selectedFloor = floor.objectId;
      this.currentFloor = floor;
      this.alreadySelected = true;
    },
    isSelectedFloor(floor) {
      if (!this.selectedFloor || !floor.objectId) {
        return false;
      }
      return this.selectedFloor == floor.objectId;
    },
    selectCamera(camera) {
      this.editMode = "EDIT";
      this.showCameraMask = true;
      this.currentCamera = camera;
      this.currentCameraX = camera.x;
      this.currentCameraY = camera.y;
      this.currentCameraAngle = camera.angle;

      setTimeout(() => {
        this.setCameraPosition(camera.x, camera.y, camera.angle);
      }, 300);
    },
    saveCamera() {
      //   console.log("Hi");
      //   console.log(JSON.stringify(this.currentCamera));
      switch (this.editMode) {
        case "EDIT":
          this.editCamera();
          break;
        case "ADD":
          this.createCamera();
          break;
      }
    },
    saveFloor: function SaveFloor() {
      SettingsService.floor_modify_floors({
        sessionId: UserService.sessionId,
        objectId: this.currentFloor.objectId,
        name: this.currentFloor.name,
        image: this.currentFloor.image.replace(
          /^data:image\/[a-z]+;base64,/,
          ""
        )
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Modify Floor Successfully.");
            this.uploadEditImg = "";
            this.getFloorList();
            return;
          }
          alert("Modify Floor Failed.");
        })
        .toPromise();
    },
    deleteFloor: function DeleteFloor() {
      SettingsService.floor_delete_floor({
        sessionId: UserService.sessionId,
        objectId: this.currentFloor.objectId
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Delete Floor Successfully.");
            this.getFloorList();
            this.currentFloor = {};
            this.alreadySelected = false;
            return;
          }
          alert("Delete Floor Failed.");
        })
        .toPromise();
    },
    createFloor() {
      SettingsService.floor_create_new_floor({
        sessionId: UserService.sessionId,
        floor: +this.newFloor.floor,
        name: this.newFloor.name,
        image: this.uploadImg.replace(/^data:image\/[a-z]+;base64,/, "")
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Create Floor Successfully.");
            this.newFloor = {};
            this.uploadImg = "";
            this.getFloorList();
            this.showCreateFloor(false);
            return;
          }
          alert("Create Floor Failed.");
        })
        .toPromise();
    },
    setImage: function(e) {
      //   console.log(e);
      const file = e.target.files[0];
      //   console.log(file);

      if (!file.type.includes("image/")) {
        alert("Please select an image file");
        return;
      }

      if (typeof FileReader === "function") {
        const reader = new FileReader();

        reader.onload = event => {
          this.uploadImg = event.target.result;
          //   console.log(this.uploadImg);
          // rebuild cropperjs with the updated source
          //   this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert("Sorry, FileReader API not supported");
      }
    },
    setUpdateImage: function(e) {
      //   console.log(e);
      const file = e.target.files[0];
      //   console.log(file);

      if (!file.type.includes("image/")) {
        alert("Please select an image file");
        return;
      }

      if (typeof FileReader === "function") {
        const reader = new FileReader();

        reader.onload = event => {
          this.currentFloor.image = event.target.result;
          // console.log(this.currentFloor.image);
          // rebuild cropperjs with the updated source
          //   this.$refs.cropper.replace(event.target.result);
        };
        reader.readAsDataURL(file);
      } else {
        alert("Sorry, FileReader API not supported");
      }
    },
    createCamera: function CreateCamera() {
      SettingsService.camera_create_camera({
        sessionId: UserService.sessionId,
        floor: this.currentFloor.objectId,
        name: this.currentCamera.name,
        sourceid: this.currentCamera.sourceid,
        x: +this.currentCamera.x,
        y: +this.currentCamera.y,
        angle: +this.currentCamera.angle
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Create Camera Successfully.");
            this.currentCamera = {};
            this.getFloorList();
            this.showCameraDetail(false);
            this.currentFloor.cameras.push(this.currentCamera);
            return;
          }
          alert("Create Camera Failed.");
        })
        .toPromise();
    },
    editCamera: function EditCamera() {
      SettingsService.camera_modify_camera({
        sessionId: UserService.sessionId,
        objectId: this.currentCamera.objectId,
        floor: this.currentFloor.objectId,
        name: this.currentCamera.name,
        sourceid: this.currentCamera.sourceid,
        x: +this.currentCamera.x,
        y: +this.currentCamera.y,
        angle: +this.currentCamera.angle
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Modify Camera Successfully.");
            this.currentCamera = {};
            this.getFloorList();
            this.showCameraDetail(false);
            return;
          }
          alert("Modify Camera Failed.");
        })
        .toPromise();
    },
    deleteCamera: function DeleteCamera() {
      SettingsService.camera_delete_camera({
        sessionId: UserService.sessionId,
        objectId: this.currentCamera.objectId
      })
        .do(response => {
          console.log(response, response.text());
          if (response.ok) {
            alert("Delete Camera Successfully.");
            this.getFloorList();
            this.currentCamera = {};
            this.showCameraDetail(false);
            return;
          }
          alert("Delete Camera Failed.");
        })
        .toPromise();
    }
  }
};
</script>

<style>
.wb-mask {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  display: none;
}

.wb-mask.active {
  display: table;
}

.wb-mask .wb-mask-item {
  max-width: 800px;
  margin: auto;
  vertical-align: middle;
  margin-top: 10%;
  border-radius: 4px;
  overflow: hidden;
}

.floor-box {
  padding: 5px;
  margin-bottom: 15px;
  background-color: #eaeaea;
  border-radius: 5px;
  color: #435964;
}
.floor-box h4 {
  margin: 0px;
}

.floor-box.active {
  background-color: #36a9e1;
  color: #fff;
}

.floor-panel {
  position: relative;
  width: 100%;
}
.floor-panel img {
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  background-color: #000;
}
.upload-panel {
  background-color: #eee;
  margin-top: 5px;
  padding: 7px;
  border-radius: 5px;
}

.camera-item {
  padding: 15px;
  background-color: #f1f1f1;
  border-radius: 5px;
  margin-bottom: 15px;
}

.camera-item h5 {
  margin: 0px;
}

.camera-info label {
  padding: 5px;
  background-color: #cfcfcf;
  margin: 5px;
  border-radius: 5px;
}

.camera-info label span {
  font-weight: bolder;
  color: #00557e;
  border: 1px solid #aaa;
  padding: 0px 5px;
  border-radius: 5px;
}

.wb-mask .camera-mask-item {
  max-width: 1280px;
  width: 95%;
  margin-top: 20px;
}

.camera-info,
.camera-info-box {
  margin-top: 15px;
}

.cemera-info-block {
  max-height: 80vh;
  overflow: auto;
}

.map-info-block {
  max-height: 60vh;
  overflow: auto;
}

.camera-position {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #37d8bf;
  border: 1px solid #1ead97;
  box-shadow: 0px 0px 5px 10px #a2f5ff8a;
  border-radius: 50px;
}

.camera-position:after {
  position: absolute;
  content: "";
  border: 60px solid transparent;
  border-right: 160px solid rgba(55, 216, 191, 0.54);
  margin-top: -37px;
  margin-left: -59px;
}
</style>



