<template>
    <div class="animated fadeIn">
        <div class="row">
            <div class="col-md-12">
                <div class="card">
                    <div class="card-block">
                        <h3 class="pull-left">Config Settings</h3>
                        <input type="button" class="btn btn-primary pull-right" value="SAVE" v-on:click="() => {seveData()}" />
                    </div>
                </div>
                <div class="card">
                    <div class="card-block">
                        <div class="row">
                            <div class="col-md-6">
                                <p> Core Config Settings</p>
                                <div class="group-box">
                                    <div class="form-group">
                                        <label>Port</label>
                                        <input type="number" class="form-control" v-model="$data.configs.core.port" />
                                    </div>
                                </div>

                            </div>
                            <div class="col-md-6">
                                <p>MongoDB Settings</p>
                                <div class="group-box">
                                    <div class="form-group">
                                        <label>IP</label>
                                        <input type="text" class="form-control" v-model="$data.configs.mongodb.ip" />
                                    </div>
                                    <div class="form-group">
                                        <label>Port</label>
                                        <input type="number" class="form-control" v-model="$data.configs.mongodb.port" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md-4">
                <div class="card">
                    <div class="card-header">
                        CMS Config Settings
                    </div>
                    <div class="card-block">
                        <div class="form-group">
                            <label>IP</label>
                            <input type="text" class="form-control" v-model="$data.configs.cms.ip" />
                        </div>
                        <div class="form-group">
                            <label>Port</label>
                            <input type="text" class="form-control" v-model="$data.configs.cms.port" />
                        </div>
                        <div class="form-group">
                            <label>Account</label>
                            <input type="text" class="form-control" v-model="$data.configs.cms.account" />
                        </div>
                        <div class="form-group">
                            <label>Password</label>
                            <input type="text" class="form-control" v-model="$data.configs.cms.password" />
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md-4">
                <div class="card">
                    <div class="card-header">
                        FRS Config Settings
                    </div>
                    <div class="card-block">
                        <div class="form-group">
                            <label>IP</label>
                            <input type="text" class="form-control" v-model="$data.configs.frs.ip" />
                        </div>
                        <div class="form-group">
                            <label>Port</label>
                            <input type="text" class="form-control" v-model="$data.configs.frs.port" />
                        </div>
                        <div class="form-group">
                            <label>WebScoket Port</label>
                            <input type="text" class="form-control" v-model="$data.configs.frs.wsport" />
                        </div>
                        <div class="form-group">
                            <label>Account</label>
                            <input type="text" class="form-control" v-model="$data.configs.frs.account" />
                        </div>
                        <div class="form-group">
                            <label>Password</label>
                            <input type="text" class="form-control" v-model="$data.configs.frs.password" />
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-md-4">
                <div class="card">
                    <div class="card-header">
                        FTS Config Settings
                    </div>
                    <div class="card-block">
                        <div class="form-group">
                            <label>Search Duration Seconds</label>
                            <input type="text" class="form-control" v-model="$data.configs.fts.searchDurationSeconds" />
                        </div>
                        <div class="form-group">
                            <label>Possible Companion Duration Seconds</label>
                            <input type="text" class="form-control" v-model="$data.configs.fts.possibleCompanionDurationSeconds" />
                        </div>
                        <div class="form-group">
                            <label>Group Information</label>
                            <div v-for="item in $data.configs.fts.groupInfo" :key="item.name">
                                <div class="group-box">
                                    <div class="box-title">{{item.name}}</div>
                                    <div class="form-group">
                                        <span v-bind:style="{backgroundColor:item.color}" class="colorSmple"></span>
                                        <label>Color</label>
                                        <input type="text" class="form-control" v-model="item.color" />
                                    </div>
                                    <div class="form-group">
                                        <span v-bind:style="{backgroundColor:item.glowcolor}" class="colorSmple"></span>
                                        <label>Glow Color</label>
                                        <input type="text" class="form-control" v-model="item.glowcolor" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import SettingsService from "../../service/settings.service";
import UserService from "../../service/user.service";

export default {
  name: "ConfigSettings",
  mounted() {
    this.initeData();
  },
  data() {
    return {
      configs: {}
    };
  },
  methods: {
    initeData() {
      //   console.log("UserService.sessionId", UserService.sessionId);
      SettingsService.config_read({ sessionId: UserService.sessionId })
        .switchMap(res => res.json())
        .map(configData => {
          const data = JSON.stringify(configData);
          this.configs = configData;
          //   console.log("configData :" + data);
        })
        .toPromise();
    },
    seveData() {
      // console.log(JSON.stringify(this.configs));

      const enrollData = this.configs;

      enrollData.core.port = +enrollData.core.port;

      enrollData.mongodb.port = +enrollData.mongodb.port;

      enrollData.cms.port = +enrollData.cms.port;
      enrollData.frs.port = +enrollData.frs.port;
      enrollData.frs.wsport = +enrollData.frs.wsport;

      enrollData.fts.searchDurationSeconds = +enrollData.fts
        .searchDurationSeconds;
      enrollData.fts.possibleCompanionDurationSeconds = +enrollData.fts
        .possibleCompanionDurationSeconds;

      SettingsService.config_modify({
        sessionId: UserService.sessionId,
        body: enrollData
      })
        .do(response => {
          if (response.ok) {
            alert("Save Data Successfully.");
            return;
          }
          alert("Save Data Failed.");
        })
        .toPromise();
    }
  }
};
</script>

<style scoped>
.group-box {
  border-radius: 4px;
  border: 1px solid #f1f1f1;
  padding: 10px;
  margin-bottom: 15px;
  overflow: hidden;
}

.box-title {
  margin: -10px;
  margin-bottom: 10px;
  background-color: #eee;
  padding: 5px 10px;
}

.colorSmple {
  width: 10px;
  height: 10px;
  display: inline-block;
  border-radius: 10px;
}
</style>



