import ApiService from '../service/api.service'
import { Observable } from 'rxjs/Rx';


export class apiProxyHelper {

  serverUrl = ''

  constructor(

  ) { }

  /**: {
  url: string,
  method?: string,
  headers?: { [key: string]: string },
  body?: any
} */
  apiProxy(args) {
    const requestInit = {
      headers: {
        'Content-Type': 'application/json',
        'accept': 'application/json'
      },
      method: args.method ? args.method : 'POST',
      body: JSON.stringify(args.body)
    };

    // // Give the default value
    // var requestSettings = requestInit;

    // if (!requestSettings) {
    //   requestSettings = Object.assign(requestInit, requestInfo);
    // }

    console.log(requestInit);

    const proxy$ = Observable.fromPromise(fetch(ApiService.serverUrl + `/${args.url}`, requestInit));
    return proxy$;
  }
}

export default new apiProxyHelper();


