import Vue from 'vue'
import Router from 'vue-router'

// Containers
import Full from 'containers/Full'

// Views
import Login from 'views/Login'
import ConfigSettings from 'views/settings/ConfigSettings'
import FloorSettings from 'views/settings/FloorSettings'
import CameraSettings from 'views/settings/CameraSettings'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      redirect: '/login',
      name: 'Login',
      component: {
        render(c) { return c('router-view') }
      },
      children: [
        {
          path: 'login',
          name: 'Login',
          component: Login
        }
      ]
    },   
    {
      path: '/',
      redirect: '/settings',
      name: 'Home',
      component: Full,
      children: [
        {
          path: 'settings',
          redirect: '/settings/config',
          name: 'Settings',
          component: {
            render(c) { return c('router-view') }
          },
          children: [
            {
              path: 'config',
              name: 'Config',
              component: ConfigSettings
            },
            {
              path: 'floor',
              name: 'Floor',
              component: FloorSettings
            },
            {
              path: 'camera',
              name: 'Camera',
              component: CameraSettings
            }
          ]
        }
      ]
    }
  ]
})



