<template>
  <navbar>
    <!-- <div class="wb-navbar"> -->
    <button class="navbar-toggler mobile-sidebar-toggler hidden-lg-up" type="button" @click="mobileSidebarToggle">&#9776;</button>
    <a class="navbar-brand" href="#"></a>
    <ul class="nav navbar-nav hidden-md-down">
      <li class="nav-item">
        <a class="nav-link navbar-toggler sidebar-toggler" href="#" @click="sidebarToggle">&#9776;</a>
      </li>
    </ul>
    <ul class="nav navbar-nav ml-auto sub-nav">
      <!-- <li class="nav-item hidden-md-down">
        <a class="nav-link" href="#">
          <i class="iconfont icon-notice"></i>
        </a>
      </li>
      <li class="nav-item hidden-md-down">
        <a class="nav-link" href="#">
          <i class="iconfont icon-message"></i>
        </a> 
      </li> 
       <li class="nav-item hidden-md-down">
        <router-link :to="'/shared/settings'"  class="nav-link">
          <i class="iconfont icon-settings"></i>
        </router-link>
      </li> -->
      <span class="avatar">
        <img  class="img-avatar" src="../../static/img/user.png">
        </span>
    </ul>
    <!-- </div> -->
  </navbar>
</template>
<script>
import navbar from "./Navbar";

export default {
  name: "header",
  components: {
    navbar
  },
  methods: {
    click() {
      // do nothing
    },
    sidebarToggle(e) {
      e.preventDefault();
      document.body.classList.toggle("sidebar-hidden");
    },
    mobileSidebarToggle(e) {
      e.preventDefault();
      document.body.classList.toggle("sidebar-mobile-show");
    }, 
    asideToggle(e) {
      e.preventDefault();
      document.body.classList.toggle("aside-menu-hidden");
    }
  }
};
</script>

<style lang="css">
.dropdown-toggle::after {
  /*display: none !important;*/
}
</style>



