<template>
  <div class="sidebar">
    <nav class="sidebar-nav">
      <ul class="nav">       
        <router-link tag="li" class="nav-item nav-dropdown" :to="{ path: '/components'}" disabled>
          <div class="nav-link nav-dropdown-toggle" @click="handleClick">FTS Settings</div>
          <ul class="nav-dropdown-items">
            <li class="nav-item">
              <router-link :to="'/settings/config'" class="nav-link" exact> Config Settings</router-link>
            </li>
            <li class="nav-item">
              <router-link :to="'/settings/floor'" class="nav-link" exact> Floor Settings</router-link>
            </li>
          </ul>
        </router-link>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  name: "sidebar",
  methods: {
    handleClick(e) {
      e.preventDefault();
      e.target.parentElement.classList.toggle("open");
    }
  }
};
</script>

<style lang="css">
.nav-link {
  cursor: pointer;
}
</style>



